$lt-blue: rgba(0, 0, 255, 0.3);
$med-blue: rgba(0, 0, 255, 0.4);
$lt-green: rgba(0, 255, 0, 0.3);
$lt-yellow: rgba(255, 255, 0, 0.4);
$lt-red: rgba(255, 0, 0, 0.3);
$dk-gray: rgba(0, 0, 0, 0.4);
$med-gray: rgba(0, 0, 0, 0.3);
$lt-gray: rgba(0, 0, 0, 0.1);
$border: rgb(109, 54, 54);

.mdb-form-wrapper {
    font-size: 0.15in;
    margin: 10px 100px;
    position: relative;
    padding: 10px 0;

    #scenarioName {
        width: 30%;
        margin-left: 7px;
    }

    label:not(.red-type) {
        white-space: nowrap; 
        width: 100%;
    }

    #debriefer-form {
        display: flex;
        flex-direction: column;
        position: relative;
        gap: 10px;

        .mdb-block-heading {
            font-weight: bold !important;
        }

        .mdb-text {
            position: relative !important;
        }
        
        .mdb-block-wrapper {
            border: 1px solid #333;
            border-radius: 5px;
            display: grid;
            grid-template-columns: repeat(6, 1fr);

            .mdb-block {
                display: flex;
                flex-direction: column;
                padding: 0.7rem;
                gap: 5px;
                position: relative;
            }

            #mdb-info {
                grid-row: 1;
                grid-column: 1 / 7;

                .mdb-info-wrapper {
                    display: flex;
                }
            }

            label {
                position: relative;
                display: flex;

                input[type="text"] {
                    position: relative;
                    width: 100%;

                    &:not(.no-margin) {
                        margin: 0 1em;
                    }
                }
            }

            #mdb-green {
                background-color: $lt-green;
                grid-row: 3;
                grid-column: 1 / 3;
            }

            #mdb-yellow {
                background-color: $lt-yellow;
                grid-row: 3;
                grid-column: 3 / 5;
            }

            #mdb-red {
                background-color: $lt-red;
                grid-row: 3;
                grid-column: 5 / 7;

                input[type="radio"] {
                    margin: 0 7px;
                }

                .mdb-red-type {
                    display: flex;
                    flex-wrap: wrap;

                    label {
                        align-items: center;
                    }
                }
            }

            #mdb-min-why {
                background-color: $lt-yellow;
                grid-row: 4;
                grid-column: 1 / 4;
            }

            #mdb-contra-why {
                background-color: $lt-red;
                grid-row: 4;
                grid-column: 4 / 7;
            }

            #mdb-findings {
                background-color: $lt-gray;
                grid-row: 2;
                grid-column: 1 / 4;
            }

            #mdb-notes {
                background-color: $lt-gray;
                grid-row: 2;
                grid-column: 4 / 7;
            }
        }
    }

    button {
        background-color: $med-gray;
        margin: 0 5px 0 0;

        &:hover {
            background-color: $dk-gray;
        }
    }

    #submit-bttn {
        background-color: $lt-blue;
        position: absolute;
        margin: 0;
        right: 0;
        bottom: 10px;
    }

    .search-results {
        position: absolute;
        background-color: #fff;
        border: 1px solid #333;
        border-radius: 5px;
        cursor: pointer;
        z-index: 100;
        max-height: 9em;
        overflow-y: auto;
        top: 100%;

        p {
            padding: 2px 5px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            width: 25em;

            &:hover {
                background-color: $lt-gray;
            }
        }
    }
}

#mdb-form-list-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 50px 0;
    gap: 10px;

    h1 {
        font-size: 3em;
    }

    #new-mdb-form {
        &:hover {
            background-color: $lt-gray;
        }
    }

    .mdb-form-list-item {
        padding: 10px;
        border: 1px solid $border;
        border-radius: 5px;
        width: 50%;
        font-size: 1.5em; 
        display: flex;
        align-items: center;

        .action-bttn {
            margin: 0 20px 0 auto; 
            background-color: $lt-gray;
            padding: 3px 0.75em;
            border-radius: 5px;

            &:hover {
                background-color: $med-gray;
            }
        }
    }
}